'use strict';

function initialize() {
    document.getElementById('download').addEventListener('click', () => {
        document.getElementById('loading').style.display = 'flex'
        chrome.runtime.sendMessage({
            message: 'work'
        })

    })

    document.getElementById('getWeb').addEventListener('click', () => {
        chrome.tabs.query({ active: true, currentWindow: true }, tabs => {
            console.log(tabs)
            let tab = tabs[0]
            chrome.cookies.getAll({
                url: tab.url
            }, cookies => {
                console.log(cookies)
            })
            chrome.tabs.sendMessage(tab.id, {
                message: 'getDocument'
            }, res => {
                let file = new File([res.document.replace('gb2312', '')], 'document.html', {
                    type: 'text/html'
                })
                let a = document.createElement('a')
                a.href = URL.createObjectURL(file)
                a.download = 'document.html'
                a.style.display = 'none'
                document.body.appendChild(a)
                a.click()
                document.body.removeChild(a)
            })
        })
    })
}

chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
    console.log('page get message')
    console.log(request)
    if (request.message == 'detectResult') {
        document.getElementById('type').innerHTML = request.result.name
        if (request.result.code == 'Unknown') {
            alert('未识别到教务，目前不支持该教务导入')
            document.getElementById('loading').style.display = 'none'
            return;
        }
    } else if (request.message == 'done') {
        document.getElementById('loading').style.display = 'none'
    } else if (request.message == 'error') {
        if (request.e.err) {
            alert(request.e.err)
        } else {
            alert('识别失败')
        }
        document.getElementById('loading').style.display = 'none'
    }
});

document.addEventListener('DOMContentLoaded', initialize);
